@import './variables.css';

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: var(--font), sans-serif;
}

ul {
  list-style: none;
}

html,
body,
.root {
  min-height: 100vh;
}

body {
  background: var(--bg-color);
}

a {
  text-decoration: none;
}

::-webkit-scrollbar {
  display: none;
}
