@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;500&display=swap);
:root {
  --light-gray-color: #c7c7c7;
  --gray-color: #6d7278;
  --dark-gray-color: #2e2f31;
  --blue-color: #4285f3;
  --black-color: #1b1c1f;
  --medium-text-size: 20px;
  --small-text-size: 12px;
  --big-title-size: 160px;
  --medium-title-size: 80px;
  --small-title-size: 40px;
  --nav-highlight-color: var(--blue-color);
  --nav-inactive-color: var(--gray-color);
  --nav-item-text-size: 15px;
  --header-color: var(--dark-gray-color);
  --bg-color: var(--black-color);
  --font: 'Noto Sans KR';
  --text-color: var(--light-gray-color);
  --bold-text-weight: 500;
  --thin-text-weight: 300;
  --btn-primary-color: var(--blue-color);
  --btn-inactive-color: var(--nav-inactive-color);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: var(--font), sans-serif;
}

ul {
  list-style: none;
}

html,
body,
.root {
  min-height: 100vh;
}

body {
  background: var(--bg-color);
}

a {
  text-decoration: none;
}

::-webkit-scrollbar {
  display: none;
}

